* {
font-family: 'Helvetica','Arial','sans-serif';
}

body{
    padding: 60px;
    background-color: black;
    color: white;
}

input{
    width: 100%;
    font-size: 1.2rem;
}

.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}


.card p{
    padding: 6px;
    text-align: center;
}

.card img{
    max-height: 170px;
}


.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}